@use '../../scss/mixins/media';

.container {
  position: relative;
  height: 50vh;
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;

  @include media.media(lg) {
    width: 100%;
  }

  &_animation {
    .container__image_type_front {
      animation: animationFront 1s ease-out forwards;
    }
    .container__image_type_back {
      animation: animationBack 1s ease-out forwards;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    overflow: hidden;
    backface-visibility: hidden;
    position: absolute;
    transition: transform 0.6s linear;
    object-fit: cover;
    display: block;
    border-radius: 8px;

    &_type {
      &_front {
        transform: perspective(600px) rotateY(0deg);
      }

      &_back {
        transform: perspective(600px) rotateY(180deg);
      }
    }
  }
}

@keyframes animationFront {
  0% {
  }
  100% {
    transform: perspective(600px) rotateY(-180deg);
  }
}

@keyframes animationBack {
  0% {
  }
  100% {
    transform: perspective(600px) rotateY(0deg);
  }
}
