@use '../../scss/mixins/media';
@use '../../scss/mixins/container';
@use '../../scss/mixins/reset';
@use '../../scss/varibles/transitions';

.container {
  @include container.container-max-widths;

  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;

  @include media.media(lg) {
    flex-direction: column;
    align-items: center;
  }

  &__information {
    align-self: stretch;
    width: 40%;
    display: flex;
    flex-direction: column;
    gap: 30px;

    @include media.media(lg) {
      align-items: center;
      width: 100%;
      gap: 15px;
    }
  }

  &__button {
    @include reset.reset-button;

    padding: 15px 35px;
    border: 1px solid #c4c8cc;
    border-radius: 5px;
    transition: transitions.$transition-main;

    @include media.media(lg) {
      align-self: center;
    }

    @media (hover: hover) {
      &:hover {
        opacity: 0.8;
      }
    }

    @media (hover: none) {
      &:active {
        opacity: 0.8;
      }
    }
  }
}
