@use '../../scss/mixins/reset';
@use '../../scss/mixins/typography';
@use '../../scss/mixins/media';

.text {
  @include reset.reset-text;
  @include typography.typography-main;

  margin: auto 0;
  text-align: justify;

  @include media.media(lg) {
    margin: 0;
    text-align: center;
  }
}
