@use './media';

// Миксин для настройки кастомной типографики
@mixin typography-custom($font-size, $font-weight, $line-height) {
  font-size: $font-size;
  font-weight: $font-weight;
  line-height: $line-height;
}

@mixin typography-main {
  @include typography-custom(14px, 400, 1.2);
}

@mixin typography-main {
  @include typography-custom(25px, 500, 30px);
  @include media.media(md) {
    @include typography-custom(20px, 500, 22px);
  }
}

@mixin typography-footer {
  @include typography-custom(14px, 500, 1.2);
}